.dropdown-ls{top:calc(100% + 1px);z-index:500;display:none;position:absolute;box-sizing:border-box;background-color:var(--sand_0);box-shadow:0 5px 20px rgba(0,0,0,.2);will-change:transform,opacity}.dropdown-ls_align_left{left:-20px}.dropdown-ls_align_right{right:-20px}.dropdown-ls__trigger{position:relative}.dropdown-ls.before-appear,.dropdown-ls.before-disappear{transition-property:opacity,transform;transition-duration:var(--menu-animation-duration);transition-timing-function:var(--menu-animation-curve)}.dropdown-ls.before-appear{opacity:0;display:flex;transform:translate3d(0, var(--menu-animation-start), 0)}.dropdown-ls.appear{opacity:1;transform:translate3d(0, 0, 0)}.dropdown-ls.visible{opacity:1;display:flex}.dropdown-ls.before-disappear{opacity:1;display:flex;transform:translate3d(0, 0, 0)}.dropdown-ls.disappear{opacity:0;transform:translate3d(0, var(--menu-animation-start), 0)}