.toggle-right{width:42px;height:24px;display:block;min-width:42px;max-height:24px;border-radius:12px;position:relative;cursor:pointer;transition:box-shadow 80ms ease;background:var(--sand_0);border:1px solid var(--sand_300)}.toggle-right__input{top:0;left:0;margin:0;opacity:0;padding:0;width:100%;height:100%;position:absolute}.toggle-right__indicator{height:24px;width:24px;display:flex;align-items:center;justify-content:center;transition:all 120ms ease}.toggle-right__indicator::before{content:"";width:10px;height:10px;border-radius:100%;transition:all 120ms ease;background:var(--sand_400);box-shadow:0 5px 10px rgba(18,17,13,.15),inset 0 -1px 0 rgba(18,17,13,.1),inset 0 0 0 1px rgba(18,17,13,.05)}.toggle-right_checked .toggle-right__indicator{margin-left:18px}.toggle-right_checked .toggle-right__indicator::before{width:16px;height:16px;background:var(--grape_500);box-shadow:0 5px 10px var(--grape_100),inset 0 -1px 0 rgba(0,0,0,.1)}.toggle-right_disabled .toggle-right__indicator{transition:none}.toggle-right_disabled .toggle-right__indicator::before{background-color:var(--sand_100);box-shadow:none;transition:none}