.create-project{flex:1;min-height:0;display:flex;flex-direction:column;overflow-y:auto}.create-project>.modal__header{border-bottom:1px solid var(--sand_300)}.create-project>.modal__header h1{width:224px;margin-right:auto;margin-bottom:0;margin-top:0;font-size:20px}.create-project>.modal__header button{line-height:1em;min-width:100px}.create-project>.modal__header .toggle-items{width:500px;margin-right:auto;box-sizing:border-box}.create-project__tab_disabled{display:flex;align-items:center;justify-content:center}.create-project__tab_disabled::before{content:"";display:block;height:8px;width:8px;border-radius:4px;margin-right:6px;background:var(--danger_color)}.create-project form.project-name{width:500px;margin:32px auto}.create-project form.project-name>*:not(:first-child){margin-top:32px}.create-project form.project-name label{display:inline-flex;align-items:center}.create-project .project-name .caption{margin-bottom:32px}