.empty-state-default{max-width:40rem;background:var(--grape_0);border:1px solid var(--grape_500);padding:2rem;border-radius:.5rem;display:flex;flex-direction:column;justify-content:center;align-items:center}.empty-state-default__icon{margin-bottom:.75rem;background:var(--grape_100);padding:.5rem;display:flex;border-radius:2rem}.empty-state-default__icon svg path{fill:var(--grape_500)}.empty-state-default__action{margin-bottom:1.25rem}.empty-state-default__title{font-size:1.75rem;font-weight:500;color:var(--sand_900);margin-bottom:1rem}.empty-state-default__description{font-size:1rem;color:var(--sand_900);margin-bottom:1rem;text-align:center;line-height:1.5em}.empty-state-default__footer{font-size:.75rem;color:var(--sand_900);text-align:center;line-height:1.5em}.empty-state-default__footer a{color:var(--grape_700);text-decoration:underline}.empty-state-default__footer a:hover{text-decoration:none}