.form{width:100%;display:block}.form__row{display:grid;justify-items:stretch;justify-content:space-between;grid-template-columns:repeat(var(--column-count, 5), 1fr);grid-gap:var(--row-gap, 8px) 8px}.form__row:not(:first-child){margin-top:12px}.form__row__description{font-size:.875rem}.form__submit{display:flex;margin-top:32px;align-items:center;justify-content:space-between}.form__submit+.inline-error{margin-top:32px}.form__submit_size_small{margin-top:16px}.form__info{display:flex;align-items:center;color:var(--danger_color);font-size:14px;line-height:22px}.form__info_valid{color:var(--sand_600)}.form__column{display:grid;grid-auto-flow:column;align-items:flex-start}.input-ls:not(.input-ls_ghost,.textarea-ls_ghost,.counter_ghost,.select-ls__list_ghost):focus,.input-ls_focused,.textarea-ls:not(.input-ls_ghost,.textarea-ls_ghost,.counter_ghost,.select-ls__list_ghost):focus,.textarea-ls_focused,.counter:not(.input-ls_ghost,.textarea-ls_ghost,.counter_ghost,.select-ls__list_ghost):focus,.counter_focused,.select-ls__list:not(.input-ls_ghost,.textarea-ls_ghost,.counter_ghost,.select-ls__list_ghost):focus,.select-ls__list_focused{outline:none;box-shadow:0 0 0 6px var(--grape_100),inset 0 -1px 0 rgba(0,0,0,.1),inset 0 0 0 1px rgba(0,0,0,.15),inset 0 0 0 1px var(--grape_100);border-color:var(--grape_100)}.form-indicator{font-weight:500}.form-indicator__item_type_success{color:var(--success_color)}.form-indicator__item_type_fail{color:var(--danger_color)}