.enterprise-badge{display:inline-block;border-radius:4px;background:linear-gradient(135deg, #FFA663 0%, #FF7557 51.56%, #E37BD3 100%);vertical-align:middle;margin:0 8px;height:20px}.enterprise-badge__label{margin:1px;background:var(--persimmon_0);color:var(--persimmon_400);border-radius:3px;font-size:11px;padding:2px 5px 2px 3px;display:flex;gap:4px;align-items:center;line-height:100%;font-weight:500}.enterprise-badge_filled .enterprise-badge__label{background:none;color:var(--persimmon_0)}.enterprise-badge_filled .enterprise-badge__icon path{fill:var(--persimmon_0)}