.input-ls,.select-ls,.textarea-ls{height:40px;min-height:40px;background:var(--sand_0);font-size:16px;line-height:22px;border:1px solid var(--sand_300);box-sizing:border-box;border-radius:5px;padding:0 16px;transition:box-shadow 80ms ease;font-weight:400}.input-ls_ghost,.select-ls_ghost,.textarea-ls_ghost{border:none;padding:0;background-color:rgba(0,0,0,0);outline:none}input.input-ls[type=radio]{width:16px;height:16px;min-height:0}.textarea-ls{padding:12px 16px;min-height:50px}