.upload_page{flex:1;min-height:0;display:flex;flex-direction:column}.upload_page__error{color:var(--danger_color);padding:0 32px}.upload_page button{line-height:1em}.upload_page>header{font-size:14px;padding:24px 32px;background:var(--sand_0);display:flex;align-items:center}.upload_page>header.overlay{box-shadow:0 2px 5px 0 rgba(0,0,0,.1)}.upload_page__url-form{display:flex}.upload_page__url-form input{border:1px solid var(--sand_300);line-height:1em;width:320px;border-radius:5px 0 0 5px}.upload_page__url-form button{margin-left:-1px;border-radius:0 5px 5px 0;background:var(--grape_500);color:var(--sand_0);cursor:pointer}.upload_page__url-form+span{margin:0 16px;color:var(--sand_600)}.upload_page__status{margin-left:auto;font-weight:500}.upload_page>main{flex:1;overflow-y:auto;background:linear-gradient(white 30%, rgba(255, 255, 255, 0)),linear-gradient(rgba(0, 0, 0, 0.1), white 100%);background-repeat:no-repeat;background-color:var(--sand_0);background-size:100% 20px,100% 5px;background-attachment:local,scroll}.upload_page table td{padding:8px 32px 8px 0}.upload_page__file-status{background:var(--kale_400);border:1px solid var(--kale_400);border-radius:4px;width:160px;height:6px;display:block}.upload_page__file-status_uploading{border:1px solid var(--grape_500);background-color:var(--grape_500);background-repeat:repeat;background-position:40px;background-size:37px 100%;animation:status-uploading 1s linear infinite;background-image:repeating-linear-gradient(-63.43deg, rgba(255, 255, 255, 0.2) 1px, #efefef 2px, #efefef 6px, rgba(255, 255, 255, 0.2) 7px, rgba(255, 255, 255, 0.2) 12px)}.upload_page__spinner{position:absolute;left:0;right:0;top:0;width:100%;height:100%;z-index:1;background:rgba(255,255,255,.5)}.upload_page__upload-button{display:flex;align-items:center;padding:8px;border:1px solid var(--grape_500);background:var(--grape_0);border-radius:4px;cursor:pointer}.upload_page__upload-icon{height:16px;margin-right:8px}.upload_page__info-icon{margin-right:8px;vertical-align:-5px;height:20px}.upload_page__csv-handling{margin:0 auto}.upload_page__csv-handling_hidden{display:none}.upload_page__csv-handling_highlighted{position:relative;z-index:12;background:var(--sand_0);padding:4px 8px;border-radius:8px}.upload_page__csv-handling label{margin-left:8px;cursor:pointer}.upload_page__csv-splash{background:var(--sand_100);position:absolute;inset:0;z-index:10;border-radius:5px;pointer-events:none}.dropzone{padding:32px;margin:0;min-height:100%;position:relative}.dropzone a{color:var(--grape_600);text-decoration:underline}.dropzone a:hover{color:var(--grape_700)}.dropzone_hovered{opacity:.5}.dropzone_hovered::before{content:"";position:absolute;inset:8px;border:2px dashed var(--grape_500);border-radius:10px;pointer-events:none}.dropzone_hovered .dropzone__content{opacity:1}.dropzone__icon{height:64px}.dropzone label{display:flex;justify-content:center}.dropzone__content{display:flex;flex-direction:column;align-items:center;cursor:pointer;background:var(--grape_0);border:1px dashed var(--grape_500);padding:2rem;width:40rem;border-radius:8px}.dropzone__content>*:not(:first-child){margin-top:32px}.dropzone__content header{font-size:24px;line-height:32px;font-weight:800;text-align:center}.dropzone__content dl{display:grid;grid-template:auto/auto auto;font-size:12px;line-height:20px;gap:0 30px}.dropzone__content dl dd{font-weight:900}.dropzone__content dl dt:last-of-type,.dropzone__content dl dd:last-of-type{margin-top:20px}.loading{display:flex;flex-direction:column;align-items:center;justify-content:center;height:50vh;max-height:300px}#import-tasks .loading h4,#import-tasks .loading p{margin-bottom:24px}@keyframes reveal{0%{opacity:0}100%{opacity:1}}#import-tasks .loading p{animation:reveal .3s 3s 1 both}.loading.splash{position:absolute;width:100%;background:var(--theme-bg);opacity:.5;z-index:1}@keyframes status-uploading{0%{background-position:0 0}100%{background-position:37px 0}}