@keyframes toast-enter-up{from{opacity:0;transform:translateY(100%)}to{opacity:1;transform:translateY(0)}}@keyframes toast-leave-fade{from{opacity:1}to{opacity:0}}@keyframes toast-leave-down{from{opacity:1;transform:translateY(var(--radix-toast-swipe-end-y))}to{opacity:0;transform:translateY(100%)}}.toast-viewport{--toast-spacing: 8px;position:fixed;display:flex;justify-content:center;width:100%;z-index:9999;bottom:24px;pointer-events:none}.toast-viewport ol{list-style:none}.toast-viewport ol li+li{margin-top:var(--toast-spacing)}.toast-viewport ol:empty{display:none}@media(prefers-reduced-motion: no-preference){.toast-viewport li{will-change:opacity,transform}.toast-viewport li[data-state=open]{animation:toast-enter-up 100ms ease-out forwards}.toast-viewport li[data-state=closed]{animation:toast-leave-fade 100ms ease-out forwards}.toast-viewport li[data-swipe=move]{transform:translateY(var(--radix-toast-swipe-move-y))}.toast-viewport li[data-swipe=cancel]{transform:translateY(0)}.toast-viewport li[data-swipe=end]{animation:toast-leave-down 100ms ease-out forwards}}.toast{--text-color: var(--sand_900);--background-color: var(--sand_100);--border-color: var(--sand_900);--hover-color: var(--sand_300);--padding: 8px 16px;display:flex;align-items:center;gap:16px;border-radius:4px;overflow:hidden;color:var(--text-color);border:1px solid var(--border-color);background-color:var(--background-color);pointer-events:all}.toast>div,.toast__content{font-size:16px;letter-spacing:.5px;line-height:24px;flex:1}.toast__action,.toast__content{padding:var(--padding)}.toast__action{font-weight:500;font-size:16px;letter-spacing:.15px;line-height:24px;border:none;border-radius:0;border-left:1px solid var(--border-color)}