.export-page__finish{width:135px}.export-page__recent{display:grid;grid-auto-flow:rows}.export-page__footer{margin:0 -32px -32px;padding:32px 40px;position:sticky;bottom:-40px;background-color:var(--sand_0);border-radius:5px}.formats{color:var(--sand_700);font-size:16px}.formats__list{margin:10px -7px}.formats a{color:var(--primary_link);text-decoration:underline}.formats__item{padding:10px 9px 10px 38px;border-radius:4px;position:relative;pointer-events:none}.formats__item_active{cursor:pointer;pointer-events:all}.formats__item_active:hover:not(.formats__item_selected){background-color:var(--grape_0)}.formats__item_selected{pointer-events:none;background-color:var(--grape_0);border:1px solid var(--grape_500)}.formats__item:not(.formats__item_active){opacity:.3}.formats__item::before,.formats__item::after{top:50%;left:10px;width:18px;height:18px;content:"";display:block;position:absolute;border-radius:100%;transform:translate(0, -50%)}.formats__item::before{box-shadow:0 0 0 .5px rgba(0,0,0,.5)}.formats__item_selected::after{left:12px;width:14px;height:14px;background-color:var(--grape_500)}.formats__name{display:flex;font-weight:500;justify-content:space-between}.formats__tag{font-size:12px;padding:2px 5px;border-radius:2px;font-weight:400;background-color:var(--grape_0)}.formats__description{font-size:14px;opacity:.7}