.modal{--transition-duration: 100ms;top:0;left:0;width:100%;height:100%;z-index:2000;display:flex;position:fixed;align-items:center;justify-content:center;background-color:rgba(18,17,13,.3);will-change:opacity}.modal_optimize .modal__wrapper{will-change:transform}.modal__wrapper{width:100%;max-height:100%;padding:40px 0;overflow:auto;box-sizing:border-box}.modal__content{width:400px;min-width:400px;min-height:100px;margin:0 auto;display:flex;flex-direction:column;background-color:var(--sand_0);border-radius:.5rem;box-shadow:0 10px 30px rgba(18,17,13,.3)}.modal__header{display:flex;min-height:40px;align-items:center;justify-content:space-between;padding:1rem 2rem;box-sizing:content-box}.modal__header_divided{border-bottom:1px solid var(--sand_300)}.modal__title{margin:0;margin-right:auto;font-size:1.75rem;font-weight:500}.modal__body{flex:1;padding:0 2rem 2rem;min-height:0;line-height:140%}.modal__body_bare{padding:0;height:100%;display:flex;flex-direction:column}.modal__footer{padding:1rem 1.5rem;text-align:center;font-size:14px;line-height:22px}.modal__footer a{color:var(--grape_600);text-decoration:underline}.modal__footer a:hover{color:var(--grape_700)}.modal__footer:not(.modal__footer_bare){background:var(--sand_100);box-shadow:inset 0 1px 0 var(--sand_300)}.modal__close{--button-color: var(--primary_link);border:none;--icon-size: 16px}.modal_fullscreen .modal__content{width:calc(100vw - 80px);height:calc(100vh - 80px)}.modal_visible{opacity:0;transition:opacity var(--transition-duration) ease}.modal_visible .modal__wrapper{transform:scale(1.05);transition:transform var(--transition-duration) ease}.modal.visible{opacity:1}.modal.visible .modal__wrapper{transform:none}.modal.before-appear{opacity:0}.modal.before-appear .modal__wrapper{transform:scale(1.05)}.modal.appear{opacity:1}.modal.appear .modal__wrapper{transform:scale(1)}.modal.before-disappear{opacity:1}.modal.before-disappear .modal__wrapper{transform:scale(1)}.modal.disappear{opacity:0}.modal.disappear .modal__wrapper{transform:scale(1.05)}