.select-ls{position:relative;font-family:Figtree,sans-serif}.select-ls__list{top:0;left:0;width:100%;height:100%;padding:0 16px;margin:0;border:none;border-radius:3px;position:absolute;background-color:rgba(0,0,0,0);transition:box-shadow 80ms ease;font-size:inherit;border-right:10px solid rgba(0,0,0,0)}.select-ls_ghost{margin-left:-10px;border:1px solid rgba(0,0,0,0)}.select-ls_ghost__list{padding:0 5px}.select-ls_ghost:hover{border:1px solid var(--sand_300)}select:disabled{background:var(--sand_200);color:var(--sand_500);font-family:Figtree,sans-serif;opacity:1}