.MessageToast{border-radius:4px;background-color:var(--background-color)}.MessageToast_info{--background-color: var(--sand_900);--hover-background-color: var(--sand_800)}.MessageToast_error{--background-color: var(--red_7);--hover-background-color: var(--red_10)}.MessageToast .toast{--border-color: var(--sand_900);--padding: 8px 16px;background-color:inherit}.MessageToast .toast__action,.MessageToast .toast__content{color:var(--sand_100);display:flex;align-items:center}.MessageToast .toast__action{background-color:inherit}.MessageToast .toast__action:hover{background-color:var(--hover-background-color)}