.heidy-tip__content{padding:1rem;border:1px solid var(--sand_300);font-size:14px;border-radius:8px;box-shadow:0 2px 6px 0 rgba(0,0,0,.1);box-sizing:border-box;background:var(--sand_0)}.heidy-tip__header{display:flex;width:100%;min-height:24px;margin-bottom:8px;flex-direction:row;justify-content:space-between}.heidy-tip__title{font-size:16px;font-weight:500;line-height:24px;letter-spacing:.15px}.heidy-tip__text{line-height:20px;letter-spacing:.25px}.heidy-tip__link{color:var(--grape_500);font-weight:500}.heidy-tip__link:hover{text-decoration:underline}.heidy-tip__link::before{content:" "}.heidy-tip__dismiss{width:24px;height:24px;padding:0;border:none;display:flex;box-shadow:none;justify-content:center;align-items:center}.heidy-tip__dismiss svg{width:14px;height:14px}.heidy-tip__dismiss svg>path{stroke:var(--sand_600)}.heidy-tip__dismiss:hover{background:var(--grape_0)}.heidy-tip__dismiss:hover svg>path{stroke:var(--grape_700)}.heidy-tip__heidi{margin-top:-12px;padding-left:16px;pointer-events:none}