.tooltip{--animation-duration: 100ms;--animation-curve: cubic-bezier(0.21, 1.04, 0.68, 1);--animation-start: -10px;top:-1000px;left:-1000px;color:var(--sand_0);display:none;z-index:10000;padding:4px 10px;font-size:14px;line-height:24px;position:absolute;pointer-events:none;background-color:var(--sand_900);border-radius:3px}.tooltip::before{left:50%;bottom:0;content:"";width:var(--pointer-size);height:var(--pointer-size);display:block;position:absolute;background-color:inherit;transform:translate(-50%, 50%) rotate(45deg)}.tooltip.before-appear,.tooltip.before-disappear{transition-property:opacity,transform;transition-duration:var(--animation-duration);transition-timing-function:var(--animation-curve)}.tooltip.before-appear{opacity:0;display:flex;transform:translate3d(0, var(--animation-start), 0)}.tooltip.appear{opacity:1;transform:translate3d(0, 0, 0)}.tooltip.visible{opacity:1;display:flex}.tooltip.before-disappear{opacity:1;display:flex;transform:translate3d(0, 0, 0)}.tooltip.disappear{opacity:0;transform:translate3d(0, var(--animation-start), 0)}.tooltip_align_top-center{--animation-start: -10px}.tooltip_align_top-center::before{bottom:0;transform:translate(-50%, 50%) rotate(45deg)}.tooltip_align_bottom-center{--animation-start: 10px}.tooltip_align_bottom-center::before{top:0;transform:translate(-50%, -50%) rotate(45deg)}